import React, { Component } from "react";
import Slider from "react-slick";
import '../View360/View360.scss';
import mall1 from '../../img/mall1.png';
import mall2 from '../../img/mall2.png';
import mall3 from '../../img/mall3.png';
import mall4 from '../../img/mall4.png';

export default class AsNavFor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nav1: null,
      nav2: null
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2
    });
  }

  render() {
    return (
      <div>
        
        <Slider className="CenterSlider"
          asNavFor={this.state.nav2}
          ref={slider => (this.slider1 = slider)}
        >

          
          <div>
          <img src={mall2} ></img>
          </div>

          <div>
            <img src={mall1} ></img>
          </div>

          <div>
            <img src={mall4} ></img>
          </div>
          </Slider>
        
        <Slider className = "center1"
          asNavFor={this.state.nav1}
          ref={slider => (this.slider2 = slider)}
          slidesToShow={3}
          swipeToSlide={true}
          focusOnSelect={true}
        >
        
          <div >
          <img src={mall2}></img>
          </div>

          <div className="demo" >
            <img src={mall1}></img>
          </div>

          <div >
            <img src={mall4}></img>
          </div>
     
        </Slider>
      </div>
    );
  }
}


