import React, { Component } from "react";
import Slider from "react-slick";
import '../CenterSlider/CenterSlider.scss';
import slider1 from '../../img/slider1.png';
import slider2 from '../../img/slider2.png';
import slider3 from '../../img/slider3.png';




export default class CenterMode extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 3,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 1000,
      focusOnSelect: true,
      pauseOnHover: true,
      className: "center",
      centerMode: true,
      centerPadding: "60px"
    };
    return (
      <div className="slick-list">
        <h2><b>Ads For Fashion's women 2019</b></h2>
        <Slider {...settings}>
          <div className="img-settings">
            <img src={slider1}  alt = "..."/>
          </div>
          <div className="img-settings" >
            <img src={slider2}  alt = "..."/>
          </div>
          <div className="img-settings">
          <img src={slider3}  alt = "..."/>
          </div>
          <div className="img-settings" >
            <img src={slider1}  alt = "..."/>
          </div>
          <div className="img-settings" >
            <img src={slider2}  alt = "..."/>
          </div>
          <div className="img-settings" >
          <img src={slider3}  alt = "..."/>
          </div>
        </Slider>
      </div>
    );
  }
}