import React, { Component } from "react";
import Slider from "react-slick";
import demo1 from '../../img/logo.png';
import Category from '../Categories/Categories';


export default class SliderView extends Component {

  render() {
    const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 2000,
      focusOnSelect: true,
      pauseOnHover: true,
      className: "center",
      centerMode: true,
      centerPadding: "60px"
    };
    return (
      <div>
    <div><h5><b>What's Hot!</b></h5></div>
        <Slider ref={slider => (this.slider = slider)} {...settings}>
          <Category>
            <img src={demo1} alt="demooz" width="100px"/> 

          </Category>
          <Category>
            <img src={demo1} alt="demooz" width="100px"/>

          </Category>
          <Category>
            <img src={demo1} alt="demooz" width="100px"/>

          </Category>
          
        </Slider>
     
      </div>
    );
  }
}