import React from 'react';
import './Categories.scss';
import CategoryTemplate from '../Categories/CategoryTemplate';
import category1 from '../../img/category-1.png';
import category2 from '../../img/category-2.png';
import category3 from '../../img/category-3.png';
import category4 from '../../img/category-4.png';

const categories = [
  {id: 0, name: 'Skin dinivia', img: category1},
  {id: 1, name: 'Makeup', img: category2},
  {id: 2, name: 'Viktor Perfume', img: category3},
  {id: 3, name: 'Vogue Perfume', img: category4}
]

const Categories = () => {
  return (
    <section className="categories">
      <div className="container">
        <h2 className="sr-only">Categories</h2>
        <ul className="list categories__list">
          {
            categories.map((category) => (
              <CategoryTemplate category={category} key={category.id} />
            ))
          }
        </ul>
        
      </div>
    </section>

  );
}

export default Categories;