
import React, { Component } from 'react'
import 'antd/dist/antd.css';
import '../View/View.scss';
import { Carousel } from 'antd';

import View360 from '../../img/View360.png';



export default class View extends Component {
    render() {
        return (
            <div>
                <Carousel  dots={false} pauseOnHover={false} autoplay={true} effect="fade">
                                    <div className="content-wrapper">
                                        <div className="text-wrapper">
                                          <h2>360 Live Experience</h2>
                                        <img src={View360} className="setting-img" text="sampldlek"/>
                                    </div>
                                    </div>
                                    <div>
                                    <img src={View360} className="setting-img" text="sampldlek"/>
                                    <div className="text-wrapper">
                                          <h2>Sample Text</h2>
                                    </div>
                                    </div>
                                    <div>
                                    <img src={View360} className="setting-img" text="sampldlek"/>
                                    <div className="text-wrapper">
                                          <h2>Sample Text</h2>
                                    </div>
                                    
                                </div>
                                   
  </Carousel>
            </div>
        )
    }
};
          