
import React, {Component} from 'react';
import 'antd/dist/antd.css';
import { Card, Col, Row } from 'antd';
import '../FlipCard2/FlipCard2.scss';

import hotoffers from '../../img/hotoffers.png';
import bestseller from '../../img/bestseller.png';
import newarrivals from '../../img/newarrivals.png';



export default class FlipCard2 extends Component {
    render() {
        return (
            <div style={{ background: 'white', padding: '60px' }}>
    <Row gutter={16}>
       <Col span={6}>
                        <Card  bordered={true} className="card-flip">
                        <li className="flip">
										<div className="front">

											<div className="card">
											<img className="card-img-top" src={hotoffers} alt="100%x180" 
											style={{height: '180px' , width: '100%' , display: 'block'}} data-holder-rendered="true"/>
											<div className="card-block">
												<h4 className="card-title">HOT OFFERS</h4>
                                            <a className="anchor" href="#" className="btn btn-primary anchor">See More</a>

											</div>
											</div>
										</div>
										<div className="back">
											<div className="card">
											<div className="card-block">
											</div>
											<img src={hotoffers} alt="Image [100%x180]" data-holder-rendered="true" 
											style={{height: '180px' , width:' 100%' , display: 'block'}}/>
											<div className="card-block">
											</div>
											</div>
										</div>
                        </li>  
                        </Card>
                    </Col>
       <Col span={6}>
                        <Card  bordered={true} className="card-flip">
                        <div className="flip">
										<div className="front">

											<div className="card">
											<img className="card-img-top" src={bestseller} alt="100%x180" 
											style={{height: '180px' , width: '100%' , display: 'block'}} data-holder-rendered="true"/>
											<div className="card-block">
												<h4 className="card-title">BEST SELLER</h4>
                                                <a href="#" className="btn btn-primary">See More</a>

											</div>
											</div>
										</div>
										<div className="back">
											<div className="card">
											<div className="card-block">
											</div>
											<img src={bestseller} alt="Image [100%x180]" data-holder-rendered="true" 
											style={{height: '180px' , width:' 100%' , display: 'block'}}/>
											<div className="card-block">
											</div>
											</div>
										</div>
                        </div>  
                        </Card>
                    </Col>
       <Col span={6}>
                        <Card  bordered={true} className="card-flip">
                        <div className="flip">
										<div className="front">

											<div className="card">
											<img className="card-img-top" src={newarrivals} alt="100%x180" 
											style={{height: '180px' , width: '100%' , display: 'block'}} data-holder-rendered="true"/>
											<div className="card-block">
												<h4 className="card-title">NEW ARRIVALS</h4>
                                                <a href="#" className="btn btn-primary">See More</a>

											</div>
											</div>
										</div>
										<div className="back">
											<div className="card">
											<div className="card-block">
											</div>
											<img src={newarrivals} alt="Image [100%x180]" data-holder-rendered="true" 
											style={{height: '180px' , width:' 100%' , display: 'block'}}/>
											<div className="card-block">
											</div>
											</div>
										</div>
                        </div>  
                        </Card>
                    </Col>


               
    </Row>
  </div>
        )
    }
}

  














